.DataListWrap,
.Content.MainContent,
.CommentsWrap {
  .BeforeCommentHeading {
    margin-bottom: 0px;
  }

  h2.CommentHeading {
    @include barlow-bold;

    color: $global-color_black_light;
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
  }

  .DataList.DataList-Search,
  .DataList.CategoryList,
  .DataList.GroupList,
  .DataList.CategoryAccordion,
  .MessageList.DataList.Comments {
    li.Item {
      border-bottom: none;
      border-top: none;
      background-color: $global-grey-very-dark-bg;
      margin-bottom: 16px;
      padding: 18px 20px 0 25px !important;
      border-radius: 10px;

      .PhotoWrap {
        top: 18px;
        left: 26px;
      }

      .Item-Header.CommentHeader {
        padding-top: 0;
        min-height: 0;

        .AuthorWrap {
          max-width: 100%;
          display: flex;
          font-family: $global-body_fontFamily;
          font-size: $global-medium_fontSize;

          .Author {
            white-space: nowrap;
          }
          .DiscussionInfo {
             text-align: right;
             white-space: nowrap;
             overflow: hidden;
             text-overflow: ellipsis;
          }
        }
        .Username {
          color: $global-color_black_light;
          font-size: 14px;
          font-weight: 500;
          line-height: 16px;
        }


        .Meta.CommentMeta.CommentInfo {
          display: block;
        }
      }

      .Message {
        font-family: $link-body_fontFamily;
      }

      .Message.userContent {
        font-size: $global-large_fontSize;
      }

      &.CategoryHeading {
        background-color: transparent;
        padding: 0 !important;
        margin: 41px 0 15px;
        color: $global-color_grey_very_dark;
        font-size: 34px;
        font-weight: 400;
        line-height: 38px;

        div {
          @include barlowcondensed-medium;
          color: $global-color_grey_very_dark;
        }

        &::before {
          content: none;
        }
      }
    }
  }

  .DataList.DataList-Search {
    li.Item {
      padding: 18px 20px 1px 25px !important;
    }
  }

 .CategoryAccordion {
   .CategoryAccordionItem {
     margin-bottom: 20px;
   }

   .ui-accordion-header-icon {
     float: right;
     top: 12px;
     background: no-repeat;
     margin-right: 25px;
   }

   .icon-chevron-down:before, .icon-chevron-up:before {
     content: " ";
     background-size: cover;
     background-image: url("/themes/topcoder/design/images/icon-chevron-down.svg");
     position: absolute;
     margin-top: 12px;
     color: #fff!important;
     height: 12px;
     width: 20px;
     display: inline-block;
   }

   .icon-chevron-down:before{

   }
   .icon-chevron-up:before {
     transform: rotate(-180deg);
   }

   .CategoryAccordionHeader {
       border-radius: 10px;
       padding: 15px 20px 15px 26px !important;
       background-color: $global-grey-very-dark-bg;

       &.ui-state-active {
           border-radius: 10px 10px 0px 0px;
       }

      a {
       @include barlowcondensed-medium;
       text-transform: uppercase;
       color: $global-color_black_light !important;
       font-size: $global-mediumTitle_fontSize !important;
     }
   }

   .DataList.CategoryList {
     li.Item {
       border-radius: 0px ;
       padding: 20px 20px 19px 26px!important;
       margin: 0px;

       &.Depth2 {
         border-right: solid 1px #E9E9E9;
         border-left: solid 1px #E9E9E9;
         border-bottom: solid 1px #E9E9E9;
         background: transparent;
       }
     }

   }

 }

  .DataList.CategoryList, .DataList.GroupList {
    li.Item {
      padding: 18px 20px 17px 25px !important;

      .Title {
        color: $global-color_black_light;
        margin-bottom: 14px;
      }

      .PhotoWrap {
        top: 0;
        left: 0;
      }

      .LatestPostTitle {
        color: $global-color_black_light;
        font-weight: 500;
      }
    }
  }
  .DataList.GroupList {
    li.Item {
      .Title {
        margin-bottom: 0px;
      }

      .Description {
        color: $global-color_grey;
      }

      .Description, .Description a {
        font-family: $global-body_fontFamily;
        font-size: 13px;
      }

      .GroupLastDiscussionComment {
         margin-top: 18px;
      }
    }
  }
}


.MessageList.Discussion {
  .Item.ItemDiscussion {
    .Item-BodyWrap {
      .Item-Body {
        .Message.userContent {
          font-family: $global-body_fontFamily;
          font-size: $global-large_fontSize;
        }
      }
    }
  }
}

.Item.ItemDiscussion, .Item.ItemComment  {
  .Item-Header {
    @media screen and (min-width: 768px) {
      .MItem.AuthorProfileStats {
        padding-right: 0px;
        margin-right: 0px;
      }

      .MItem.DateCreated {

        padding-left: 0px;
        margin-left: 0px;

        &::before {
          content: '  |  ';
          font-family: $global-body_fontFamily;
          font-size: 12px;
        }
      }
    }
  }
}


.Message.userContent, .CommentFormWrap .Preview {
   a, a:hover, a:focus {
    font-size: $global-large_fontSize;
    color: $global-blue;
    text-decoration: underline;
  }
}




.ReactButton.Quote.js-quoteButton {
  font-size: 12px;
  font-weight: 400;
  letter-spacing: 0.44px;
  line-height: 15px;
}

.MessageForm.CommentForm.FormTitleWrapper {
  .H {
    margin-bottom: 27px;
  }
  h2 {
    text-transform: uppercase;
    color: $global-color_black_light;
    font-family: $global-heading_fontFamily2;
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
  }
}


.FormTitleWrapper {
  .Buttons {

    .Back {
      margin-right: 20px !important;
      * {
        color: $global-blue;
        font-family: $global-body_fontFamily;
      }
    }
  }
}

// mobile
@media screen and (max-width: 768px) {
  .DataListWrap,
  .Content.MainContent,
  .CommentsWrap {
    .DataList.DataList-Search,
    .DataList.CategoryList, .DataList.CategoryAccordion
    .MessageList.DataList.Comments {
      li.Item {
        .PhotoWrap {
         // left: 0;
        }

        .AuthorWrap {
          margin-top: 3px;
        }

        &.CategoryHeading {
          background-color: transparent;
          margin: 0 !important;
          padding: 41px 0 15px !important;
          color: $global-color_black_light;

          div {
            color: $global-color_black_light;
          }
        }
      }
    }
  }

  .MessageList.Discussion {
    .Discussion {
      .Item-Header {
        .AuthorWrap {
          margin-top: 3px;
        }
      }
    }
  }

  .DataListWrap,
  .Content.MainContent,
  .CommentsWrap {
    .DataList.DataList-Search {
      li.Item {
        &:first-child {
          border-top: 1px solid $global-grey-bg;
        }
        &:last-child {
          padding-bottom: 16px !important;
        }
        h3 {
          margin-bottom: 2px;
          line-height: 30px;

          a {
            color: $global-color_black_light;
            font-size: 20px;
          }
        }

        .Item-Body.Media {
          margin-bottom: 0;

          .Media-Body {
            * {
              font-size: 14px;
            }

            .Meta {
              margin-top: 0;
              height: 22px;
            }

            .Summary {
              margin-top: 2px;
              height: 22px;
            }
          }
        }
      }
    }
  }
}
